.gatsby-link {

  font-size: 1.2857rem;
  font-weight: 400;
  font-style: normal;
  text-decoration: none;
  color: #262c37;
  display: inline-block;
  margin-bottom: 1rem;
  cursor: pointer;
  transition: color 200ms ease;
  @media (hover: hover) {
    &:hover {
      color: #90c53d;
    }
  }
  @media (max-width: 1199px) {
    font-size: 1.1428rem;
  }
  @media (max-width: 991px) {
    font-size: 1rem;
  }
  @media (max-width: 812px) and (orientation: landscape),
    (max-width: 767px) and (orientation: portrait) {
    font-size: 0.8571rem;
  }

}