
  .activeLink {
    color: #90c53d;
    > span {
      position: relative;
      &:before {
        left: 50%;
        transform: translate(-50%, 200%);
        bottom: 0;
        position: absolute;
        display: block;
        content: "";
        width: 0.2857rem;
        height: 0.2857rem;
        background-color: #90c53d;
        border-radius: 50%;
      }
    }
  }
  .mobileActiveLink {
    color: #90c53d;
  }
  .coloredNav {
    box-shadow: 0 8px 8px 0 rgba(10, 18, 33, 0.05);
    background: rgba(255, 255, 255, 1);
  }
  .withoutAdditionalMenuAndIsActive {
    cursor: default;
  }
  .hamburger {
    margin-left: auto;
    display: none;
    @media (max-width: 991px) {
      display: block;
    }
  }
