.news-container {

  width: 28.5714rem;
  height: 26rem;
  position: relative;
  @media (max-width: 991px) {
    width: 22.8571rem;
    height: 24.3571rem;
  }
  @media (max-width: 767px) and (orientation: portrait) {
    display: flex;
    justify-content: center;
    width: 100%;
    max-height: 18.0714rem;
    margin-bottom: 3.4rem;
  }
  @media (max-width: 812px) and (orientation: landscape) {
    height: 100%;
    max-height: 18.0714rem;
    margin-bottom: 1.4rem;
  }

}