.menu {

  z-index: 1;
  list-style: none;
  display: flex;
  padding: 0.4457rem 0 0 0;
  margin: 0 0 0 auto;
  @media (max-width: 991px) {
    display: none;
  }
  > li {
    > a {
      transition: color 200ms ease;
      @media (hover: hover) {
        &:hover {
          color: #90c53d;
        }
      }
    }

    > menu {
      a {
        white-space: nowrap;
      }
    }
  }

}