.mobile-menu {

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 0;
  z-index: 1;
  list-style: none;
  padding: 1.4714rem 0 0 0;
  flex-shrink: 0;
  flex-grow: 1;
  width: 100%;
  @media (max-width: 812px) and (orientation: landscape) {
    padding: 1rem 0 0 0;
  }
  @media (min-width: 992px) {
    display: none;
  }

}