.link {

  color: #262c37;
  display: block;
  margin-bottom: 1rem;
  cursor: pointer;
  transition: color 200ms ease;
  @media (hover: hover) {
    &:hover {
      color: #90c53d;
    }
  }
  &.isActive {
    cursor: default;
    color: #90c53d;
    > span {
      cursor: default;
      color: #90c53d;
    }
  }

  &.little {
    margin: 0.4rem 0 0 0.5rem;
    font-size: 0.8571rem;
  }

  &:last-child {
    margin-bottom: 0;
  }
  &.title {
    cursor: default;
    @media (hover: hover) {
      &:hover {
        color: #262c37;
      }
    }
    &.isActive {
      @media (hover: hover) {
        &:hover {
          color: #90c53d;
        }
      }
    }
  }

}