
  .fadeIn {
    animation-name: fadeIn;
    animation-duration: 200ms;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    @keyframes fadeIn {
      from {
        opacity: 0;
      }
      to {
        opacity: 1;
      }
    }
  }
  .fadeOut {
    animation-name: fadeOut;
    animation-duration: 200ms;
    animation-timing-function: linear;
    animation-iteration-count: 1;
    @keyframes fadeOut {
      from {
        opacity: 1;
      }
      to {
        opacity: 0;
      }
    }
  }
