.scroll-bar {

  width: 100%;
  height: 100%;
  :global(.scroll-content) {
    width: 100%;
    height: 100%;
    will-change: transform;
    > div:first-child {
      width: 100%;
      height: 100%;
    }
  }

  &.disableHover {
    * {
      pointer-events: none;
    }
  }

  :global(.scrollbar-track) {
    display: none !important;
  }

}