.scroll-bar-container {

  background-color: #fff;
  > div {
    :global(.scroll-content) {
      will-change: transform;
    }
    :global(.scrollbar-track) {
      display: none !important;
    }
  }
  &.withScrollbarY {
    > div {
      &:nth-child(2) {
        display: none !important;
      }
      &:last-child {
        display: block !important;
      }
    }
  }

}