
  .longreadCloseBtn {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  a.arrowBtn {
    top: 50%;
    transform: translateY(-50%);
    width: auto;
  }

  .leftArrowBtn {
    left: 3.4rem;
    svg {
      margin-right: 0.6rem;
    }
  }
  .rightArrowBtn {
    right: 3.4rem;
    svg {
      margin-left: 0.6rem;
      transform: rotate(180deg);
    }
  }

  @media (max-width: 1199px) {
    .leftArrowBtn {
      left: 1.8rem;
    }
    .rightArrowBtn {
      right: 1.8rem;
    }
  }

  @media (max-width: 767px) and (orientation: portrait) {
    a.arrowBtn {
      font-size: 0;
    }
    .leftArrowBtn {
      svg {
        margin-right: 0;
      }
    }
    .rightArrowBtn {
      svg {
        margin-left: 0;
      }
    }
  }
